import { addArticle, getArticle, updateArticle } from "@/api/article";

export default {
  // 在路由匹配组件渲染之前会先执行中间件处理
  middleware: "authenticated",
  name: "editorIndex",
  data() {
    return {
      article: {
        title: "",
        description: "",
        body: "",
        tagList: [],
      },
      tag: "",
      disabled: false,
      errors: [],
    };
  },
  async asyncData({ params }) {},
  mounted() {
    this.resetForm();
    this.getArticle()
  },
  methods: {
    // 添加tag列表
    addTagList() {
      this.article.tagList.push(this.tag);
      this.tag = "";
    },
    // 移除某个tag
    delTag(idx) {
      this.article.tagList.splice(idx, 1);
    },
    // 发布文章
    async publidArticle() {
      this.disabled = true;
      try {
        const p = {
          article: this.article,
        };
        let data = {};
        if (this.$route.params.slug) {
          const slug = this.$route.params.slug;
          data = await updateArticle(slug, p);
        } else {
          data = await addArticle(p);
        }
        const slug = data.data.article.slug;
        this.disabled = false;
        this.resetForm();
        this.$router.push({
          name: "article",
          params: {
            slug,
          },
        });
      } catch (error) {
        this.disabled = false;
        this.errors = error.response.data.errors;
      }
    },
    // 获取文章
    async getArticle() {
      if (this.$route.params.slug) {
        const slug = this.$route.params.slug
        const { data } = await getArticle(slug);
        const { title, description, body, tagList } = data.article;
        this.article = {
          title,
          description,
          body,
          tagList,
        };
      }
    },
    // 重置表单
    resetForm() {
      this.article = {
        title: "",
        description: "",
        body: "",
        tagList: [],
      };
      this.tag = "";
      this.errors = [];
    },
  },
};
